<template>
	<div>
	<div class="r_title">
				<h1>{{msg}}</h1>
				<a href="">更多</a>
	</div>
		<div class="reying">
			<div class="loading" v-if="subjects[0].loading">
				<img src="../assets/images/loading.gif" alt="">
			</div>
			<div class="r_movie" v-else>
				<div class="r_movie_content" v-for="subject in subjects[1]">
					<router-link :to="{name:'movieDetail',params:{id:subject.id}}">
						<img :src="subject.images.medium" alt="">
						<p class="r_movie_title" v-text="subject.title"></p>
						<div class="r_rating" >
							<span v-for="n in 5" class="r_stars" :class="{light : n*10<=subject.rating.stars, grey :n*10>subject.rating.stars}">
							</span>
							<span v-text="subject.rating.average"></span>
						</div>
					</router-link>
				</div>
			</div>
		</div>
	</div>

</template>

<script>

	export default {
		name:'movie',
		props:['msg','subjects'],
		data () {
			return {
				
			}
		},	
	}
</script>

<style lang="scss">
	.r_title{
			width:9.16rem;
			height:0.906667rem;
			margin:0.266667rem auto;
		
			h1{
				font-size:0.48rem;
				display:inline-block;
			}
			a{
				float:right;
				font-size:0.4rem;
				color:#92d99e;
			}
		}
	.reying{
		width:100%;
		overflow-x:scroll;
		
		.loading{
			img{
				width:100%;
				height:5.106667rem
			}
		}
		.r_movie{
			width:29.333333rem;
			height:5.106667rem;
			padding-left:0.466667rem;
			.r_movie_content{
				display:inline-block;
				vertical-align:top;
				margin-right:0.133333rem;
				a{
					color:#000;
				}
				img{
					width:2.666667rem;
					height:3.813333rem;
				}
				.r_movie_title{
					text-align:center;
				}
				.r_rating{
					padding-left:0.48rem;
					.r_stars{
						display:inline-block;
						vertical-align:top;
						width:0.266667rem;
						height:0.266667rem;
						background-size:cover;
						margin-top:0.066667rem;
					}
					.light{
						background-image:url("data:image/png;base64,")
					}
					.grey{
						background-image:url("data:image/png;base64,")
					}
				}
			}

		}
	}
	.reying::-webkit-scrollbar{
		  display:none;
		}
</style>



